.PageDescription{
  text-align: center;
  display: flex;
  align-items:center;
  justify-content: flex-end;
}
.PageDescription p{
  margin: 1% 28%;
  font-size: .8em;
}
.PageContent-img{
  width: 30%;
  display: flex;
  justify-content: flex-end;
}
.PageContent-img img{
  width: 90%;
  height: 100%;
  box-shadow: 0px 1px 10px 0px black;
}
.pageContent-text{
  width: 60%;
}
.PageDescription--CoffeHouse .pageContent-text{
  width: 100%;
}
.PageDescription--ForYourPleasure p {
  margin: 3% 28%;
}
.PageDescription--ForYourPleasure img{
 box-shadow:none;
}

@media screen and (max-width:768px) {
  .PageContent-img{
    display: none;
  }
  .pageContent-text{
    width: 100%;
  }
  .PageDescription p{
    margin: 2% 10%;
    font-size: .8em;
  }
}